import axios from 'axios'

export default {
    namespaced: true,
    state() {
        return {
            page: 1,
            urlPosts: ``,
            posts: [],
            loader: false,
        }
    },
    mutations: {
        setUrlPosts(state) {
            //state.urlPosts = `https://web.dmd-life.ru/wp-json/wp/v2/posts?per_page=100`
            state.urlPosts = `https://dmd-life-default-rtdb.europe-west1.firebasedatabase.app/posts.json`
        },
        getCatNames(post) {
            this.state.post.catsName = []
            const idPost = post
            const obj = {}
            this.state.post.postData.categories.forEach((i) => {
                this.state.cats.catsAside.filter((arr)=> {
                    if (arr.id == i) {
                        this.state.post.catsName.push(arr.name)
                    }
                })
            })
        }
    },
    actions: {
        async getPosts({commit}) {
          if(this.state.home.posts.length === 0 ) {

              this.state.home.loader = true
              commit('setUrlPosts')
              const url = this.state.home.urlPosts
              await fetch(url)
                  .then(response => {
                      if (!response.ok) throw Error(response.data)
                      return response.json()
                  })
                  .then(data => {
                      this.state.home.posts = data
                      if (window.location.pathname.substr(1,4) == 'post') {
                          this.state.home.posts.filter((arr) => {
                              if (arr.id == window.location.pathname.substr(5)) {
                                  this.state.post.postData = arr
                                  commit('getCatNames')
                              }
                          })
                          this.state.post.loader = false
                      }
                  })
                  .catch(error => console.dir(error))
              this.state.home.loader = false
              this.state.homeApi = false
          }
        }
    }
}