export default {
    namespaced: true,
    state() {
        return {
            cats: [
                {id: 90, name: "Акции"},
                {id: 40, name: "Афиша"},
                {id: 1, name: "Без рубрики"},
                {id: 50, name: "Интересное"},
                {id: 89, name: "Кино"},
                {id: 91, name: "Мероприятия"},
                {id: 5, name: "Новости"},
                {id: 72, name: "Новости города"},
                {id: 75, name: "Новости региона"},
                {id: 73, name: "Политика"},
            ],
            catsAside: []
        }
    },
    actions: {
        async getCats({commit}) {
            //const url = 'https://web.dmd-life.ru/wp-json/wp/v2/categories'
            const url = 'https://dmd-life-default-rtdb.europe-west1.firebasedatabase.app/categories.json'
            await fetch(url)
                .then(response => {
                    if (!response.ok) throw Error(response.data)
                    return response.json()
                })
                .then(data => {
                    this.state.cats.catsAside = data
                })
                .catch(error => console.dir(error))
            this.state.tagsApi = false
        }
    }
}