export default {
    namespaced: true,
    state() {
        return {
            postData: {},
            loader: true,
            urlPost: '',
            catsName: []
        }
    },
    mutations: {
      setUrlPosts(state, id) {
          state.urlPost = `https://web.dmd-life.ru/wp-json/wp/v2/posts/${id}`
      },
        getCatNames(post) {
            this.state.post.catsName = []
            const idPost = post
            const obj = {}
            this.state.post.postData.categories.forEach((i) => {
                this.state.cats.catsAside.filter((arr)=> {
                    if (arr.id == i) {
                        this.state.post.catsName.push(arr.name)
                    }
                })
            })
            console.log(this.state.post.catsName)
        }

    },
    actions: {
        async getPost() {
            this.state.post.loader = true
            const url = this.state.post.urlPost
            await fetch(url)
                .then(response => {
                    if (!response.ok) throw Error(response.data)
                    return response.json()
                })
                .then(data => {
                    this.state.post.postData = data
                })
                .catch(error => console.dir(error))
        }
    }
}