export default {
    namespaced:true,
    state() {
        return {
            weatherNow: '',
            main: {
                clear: "солнечно",
                rain: "дождь",
                snow: "снег",
                clouds: "облачно",
                overcastClouds: "пасмурно"

            }
        }
    },
    actions: {
        async getWather() {
            const url = 'https://api.openweathermap.org/data/2.5/forecast?id=565381&appid=7ec574b7f6ca35c0115ed939aea16738&units=metric'
            await fetch(url)
                .then(response => {
                    if(!response.ok) throw Error(response.data)
                    return response.json()
                })
                .then(data => {
                    this.state.weather.weatherNow = data.list[0].main.temp
                })
                .catch(error => console.dir(error))
        }
    }
}